import React from "react";
import "./MoviesCard.css";

function MoviesCard(props) {
  function handleSubmit() {
    props.onAddMovie(props);
  }

  function handleDelete() {
    props.onDeleteMovie(props);
  }

  function handleButtonClick(e) {
    e.preventDefault();
    if (!props.isClicked) {
      handleSubmit();
    } else {
      handleDelete();
    }
  }

  console.log(props.location);

  return (
    <>
      <li className="list__item">
        <div className="list__container">
          <p className="list__name">{props.nameRU}</p>
          <p className="list__info">{props.duration}</p>
        </div>
        <a href={props.trailerLink} target="_blank" rel="noreferrer">
          <img
            className="list__image"
            alt=" "
            src={props.imageUrl}
          ></img>
        </a>
        <form className="list__form" onSubmit={handleButtonClick}>
              <button
                className={`list__button  ${
                  props.isClicked && props.location.pathname === '/movies'
                    ? "list__button_active"
                    : "list__button_inactive"
                } ${props.location.pathname === '/saved-movies' ? 'list__button_saved' : ''} `}
                type="submit"
                name="save"
                children={`${props.isClicked ? "" : ""}`}
              ></button>
        </form>
      </li>
    </>
  );
}

export default MoviesCard;
